.character {
  &__wrapper {
    display: grid;
    grid-template-columns: 300px 1fr;
    gap: clamp(20px, 2.5vw, 40px);

    @media (max-width: 992px) {
      grid-template-columns: 1fr;
    }
  }

  &__aside {
    @media (max-width: 992px) {
      display: none;
    }
  }
  .banner-section__btn {
    background: var(--siniy);
    color: #fff;
  }

  .character__table {
    margin-top: 30px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5px;
    font-family: var(--font-family);

    @media(max-width: 992px) {
      display: none;
    }
  }

  .character-mobile-table {
    display: none;

    @media(max-width: 992px) {
      display: grid;
      gap: 4px;
      margin-top: 20px;
    }

    .character-mobile-table-item {
      border-radius: 8px;
      padding: 10px;
      background: var(--fon);
      display: flex;
      flex-direction: column;
      gap: 14px;

      .top-title {
        font-family: var(--font-family);
        font-weight: 700;
        font-size: 16px;
        line-height: 120%;
        color: var(--chernyy);
      }

      .desc {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 14px;
        line-height: 130%;
        color: var(--chernyy);
      }

      ul {
        display: flex;
        flex-direction: column;
        gap: 3px;
        padding-left: 10px;
        li {
          font-family: var(--font-family);
          font-weight: 400;
          font-size: 14px;
          line-height: 130%;
          color: var(--chernyy);
          position: relative;
          padding-left: 10px;
          &:before {
            content: '';
            position: absolute;
            top: 5px;
            left: 0;
            width: 4px;
            height: 4px;
            background: #000;
            border-radius: 50%;
          }
        }
      }

     
    }

     .character-mobile-table-link {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 14px;
        line-height: 130%;
        color: var(--chernyy);
        border: 1px solid var(--siniy);
        border-radius: 8px;
        padding: 8px 20px;
        display: block;
        text-align: center;
        margin-top: 16px;
      }
  }

  .mobile-compare-table {
    margin-top: 50px;
    display: none;
    @media(max-width: 992px) {
      display: block;
    }
    .mobile-compare-table-title {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: 24px;
      text-transform: uppercase;
      color: var(--chernyy);
      margin-bottom: 24px;
    }
    .mobile-compare-table-items {
      display: grid;
      gap: 4px;
    }
    .mobile-compare-table-item {
        border-radius: 8px;
        padding: 10px;
        background: var(--fon);
        display: flex;
        flex-direction: column;
        gap: 14px;

        .top-title {
          font-family: var(--font-family);
          font-weight: 700;
          font-size: 16px;
          line-height: 120%;
          color: var(--chernyy);
        }

        .info {
          display: flex;
          justify-content: space-between;
          border-radius: 8px;
          padding: 4px 16px;
          background: #fff;
        }
        .desc {
          font-family: var(--font-family);
          font-weight: 400;
          font-size: 14px;
          line-height: 130%;
          color: var(--chernyy);
        }
    }
  }

  .character-mobile-table-link {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 14px;
        line-height: 130%;
        color: var(--chernyy);
        border: 1px solid var(--siniy);
        border-radius: 8px;
        padding: 8px 20px;
        display: block;
        text-align: center;
        margin-top: 16px;
  }

  

  .character__table thead,
  .character__table tbody,
  .character__table tr {
    display: contents;
  }

  .character__table th,
  .character__table td {
    border-radius: 8px;
    padding: 14px 20px;
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    color: var(--chernyy);
    background: var(--fon);
  }

  .character__table th {
    background: linear-gradient(132deg, #e3e8f3 0%, #c1d2f6 100%);
    font-weight: bold;
    text-align: left;
  }

  .character__table ul {
    margin: 0;
    padding-left: 20px;
  }

  .character__table li {
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    color: var(--chernyy);
    margin-bottom: 5px;
  }

  .compare__table {
    grid-template-columns: repeat(4, 1fr);

    @media(max-width: 767px) {
      grid-template-columns: 1fr 1fr;

      thead {
        display: none;
      }
    }
  }
}
