                gap: 10px;
                margin-top: 14px;
                @media(max-width: 767px) {
                    grid-template-columns: 1fr;
                }
                .btn--accent {
                    padding: 19px;
                    font-size: 14px;
                }

                .download-btn {
                    font-family: var(--font-family);
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 130%;
                    text-decoration: underline;
                    text-decoration-skip-ink: none;
                    color: var(--chernyy);
                    border-radius: 8px;
                    padding: 8px 20px;
                    background: var(--hover-golubogo);
                    display: flex;
                    align-items: center;
                    gap: 10px;
                    transition: .3s ease;
                    justify-content: center;
                    .icon {
                        background: #fff;
                        width: 40px;
                        height: 40px;
                        display: grid;
                        place-items: center;
                        border-radius: 4px;
                    }

                    @media(any-hover: hover) {
                        &:hover {
                            color: var(--belyy);
                            background: var(--siniy);
                        }
                    }
                }
            }
        }
    }

    .roof-sizes {
        p {
            font-weight: 400;
            font-size: 14px;
            margin: 0;
            margin-bottom: 10px;
        }
    }

    .radio-block {
        .radio-items {
            display: flex;
            flex-wrap: wrap;
            flex-direction: row;
            .radio-item {
                max-width: fit-content;
            }
        }
    }
    .calculator__tab .right {
        @media(max-width: 992px) {
            display: block;
        }
    }

    .select-options {
        display: flex;
        flex-direction: column;
        gap: 10px;

        .select-option {
            width: 100%;
            position: relative;
            &:after {
                content: '';
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                right: 20px;
                width: 9px;
                height: 9px;
                background: url('../../img/icons/arrow-city-fill.svg') no-repeat;
                background-size: contain;
            }
            select {
                appearance: none;
                border-radius: 8px;
                padding: 14px 24px;
                background: #fff;
                font-family: var(--font-family);
                font-weight: 400;
                font-size: 14px;
                line-height: 130%;
                color: var(--chernyy);
                width: 100%;
                border: none;
            }
        }
    }
}